.youtube-embed {
    //background-color: #292F33 !important;
    background-color: white;
    min-height: 70vh;
    height: 77vh;
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 992px) {
        min-height: 50vh;
        height: 50vh;
    }

    .youtube-embed-container {
        height: 80%;
        width: 55%;
        border: 3px solid white;

        @media screen and (max-width: 992px) {
            width: 100%;
            height: 40%;
        }

        .container {
            height: 100%;

            iframe {
                width: 100%;
                height: 100%;
            }
        }
    }
}